  export default {
    data(){
			return{
				worthNavData:{},
				worthPoolData:[]
			}
		},
		onLoad(){
			uni.request({
				url:"https://m.you.163.com/topic/v1/know/navWap.json",
				success:(data)=>{
					this.worthNavData = data.data.data;
					console.log(data);
					},
				fail:()=>{console.log("请求worth-nav失败")}
			}),
			uni.request({
				url:"https://m.you.163.com/topic/v1/find/recManual.json",
				success:(data)=>{
					this.worthPoolData = data.data.data;
					console.log(data);
					},
				fail:()=>{console.log("请求worth-pool失败")}
			})
		},
		methods:{
			goHome(){
				uni.switchTab({
					url:"../Home/index"
				})
			},
			goSearch(){
				uni.navigateTo({
					url:"../Search/index"
				})
			},
			goCart(){
				uni.switchTab({
					url:"../Cart/index"
				})
			}
		}
  }
